*{
  margin: 0;
  box-sizing: border-box;
}
body{
  margin-top: 10%;
  background-color:aquamarine;
}
ul{
  list-style-type: none;
  display: block
}
h3{
  margin-left:15%;
  padding-bottom: 5%;
}
li{
  width: 40%;
  border-style: none;
  background-color: aliceblue;
  padding: 10px 10px 10px 10px;
  margin-bottom: 20px;
  border-radius: 7px;
  display: flex;
  
}
img{
  border-radius: 50%;
}
a{
  text-decoration: none;
  color: gray;
}
.login{
  text-align: center;
  margin: auto;
}